<template>
  <section class="home-right-wrap">
    <div class="search-header">
      <div class="title">北上资金动向查询</div>
      <div style="padding-bottom: 10px;">
        <span>变化趋势:</span>
        <a-select style="width: 150px;margin:0 50px 0 16px;" placeholder="请选择变化趋势" v-model='updateNum'>
          <a-select-option value="1">连续增仓</a-select-option>
          <a-select-option value="2">连续减仓</a-select-option>
        </a-select>
        <span>时间周期:</span>
        <a-select style="width: 80px;margin-left: 20px;" v-model='selectNum'>
          <a-select-option value="1">1</a-select-option>
          <a-select-option value="2">2</a-select-option>
          <a-select-option value="3">3</a-select-option>
        </a-select>
        <a-select style="width: 80px;margin:0 50px 0 20px;" v-model='selectType'>
          <a-select-option value="1">日</a-select-option>
          <a-select-option value="7">周</a-select-option>
        </a-select>
        <a-button style="margin-right: 20px;" @click="handleReset">重置</a-button>
        <a-button type='primary' @click="getData">查询</a-button>
      </div>
      <div class="table-title">查询结果({{pagination.total}})</div>
    </div>
    <a-table
      :columns="columns"
      :rowKey="record => record.idx"
      :dataSource="tableData"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
      <template slot="name" slot-scope="name, record">
        <a @click="handleClickName(record)">{{record.name}}</a>
      </template>
    </a-table>
  </section>
</template>
<script>
import {
  getTrandData // 沪深港动向
} from '@/api/index'
import moment from 'moment'
const columns = [
  { align: 'center', title: '代码', dataIndex: 'code' },
  { align: 'center', title: '上市公司', dataIndex: 'name', scopedSlots: { customRender: 'name' } },
  { align: 'center', title: '持股数量', dataIndex: 'holding_num', sorter: true },
  { align: 'center', title: '持股市值', dataIndex: 'holding_value', sorter: true },
  { align: 'center', title: '持股数量占A股百分比(%)', dataIndex: 'holding_num_percent', sorter: true },
  { align: 'center', title: '变化比例(%)', dataIndex: 'changePercent', sorter: true }
]
export default {
  name: 'trend',
  data() {
    return {
      // time: moment().format('YYYY-MM-DD'),
      time: '2019-04-01',
      updateNum: '1', // 1增仓 2减仓
      selectType: '1', // 1日， 7周
      selectNum: '1', // 几日、几周
      tableData: [],
      holdingNum: undefined, // 持股数量 1-升序，2-降序（一次只能传一个)
      holdingValue: undefined, // 持股数值
      holdingNumPercent: undefined, // 持股百分比
      holdingChange: undefined, // 持股变化比例
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      loading: false,
      columns
    }
  },
  created() {
    this.getData()
  },
  methods: {
    handleClickName(record) {
      this.$router.push('/stockInfo?code=' + record.code)
    },
    handleTableChange(pagination, filters, sorter) {
      this.holdingNum = (sorter.columnKey === 'holding_num' && (sorter.order === 'ascend' ? 1 : 2)) || undefined
      this.holdingValue = (sorter.columnKey === 'holding_value' && (sorter.order === 'ascend' ? 1 : 2)) || undefined
      this.holdingNumPercent = (sorter.columnKey === 'holding_num_percent' && (sorter.order === 'ascend' ? 1 : 2)) || undefined
      this.holdingChange = (sorter.columnKey === 'changePercent' && (sorter.order === 'ascend' ? 1 : 2)) || undefined
      this.pagination = { ...pagination }
      this.getData()
    },
    getData() {
      console.log(moment().format('YYYY-MM-DD'))
      let data = {
        time: this.time,
        selectType: this.selectType,
        selectNum: this.selectNum,
        updateNum: this.updateNum,
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize,
        holdingNum: this.holdingNum,
        holdingValue: this.holdingValue,
        holdingNumPercent: this.holdingNumPercent,
        holdingChange: this.holdingChange
      }
      this.loading = true
      getTrandData(data).then(res => {
        console.log('沪深港动向')
        console.log(res)
        if (res.code === 200) {
          let obj = res.data.data
          for (let i in obj) {
            obj[i].idx = i
          }
          this.tableData = obj
          const pagination = { ...this.pagination }
          pagination.total = res.data.total
          this.pagination = pagination
        }
        this.loading = false
      })
    },
    // 重置
    handleReset() {
      this.selectType = '1'
      this.selectNum = '1'
      this.updateNum = '1'
      this.getData()
    }
  }
}
</script>
