    min-height: 500px;
    width: 1030px;
    background-color: #fff;
    border: 1px solid #E4E5E7;
    .search-header {
      padding: 0 20px;
      .title {
        padding: 20px 0;
        font-size: 18px;
        color: #262626;
      }
      .table-title {
        padding: 20px 0;
        font-size: 18px;
        color: #262626;
      }
    }
}
</style>
